const api = (request) => () => {
  const variables = JSON.stringify({});
  const query = `
  query{
    getPriceDisplayOnDailyReportDefault {
      isPriceDisplayOnDailyReport
      isServiceTicketMerged
      isHelperPointPriceDisplay
    }
  }
  `;
  return request({
    method: 'post',
    data: { query, variables },
  });
};
export default api;
